.wrapper {
  padding-top: 50px;
  background: #cc0000;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

h1 {
  text-align: center;
  font-size: 40px;
}

h2 {
  text-align: center;
}

h3 {
  text-align: center;
}

 body {
   background-color: #4747d1;
 }
.card {
  background: #fff;
  border-radius: 0.5em;
  height: 13em;
  width: 13em;
  margin: 1rem;
  position: relative;
  box-shadow: 0 3px 6px #000000, 0 3px 6px black;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: white;
}

.card > .img-container > img {
  width: 100%
}

